<template>
  <Card>
    <p slot="title">
      用户编辑
    </p>
    <Form
      ref="formValidate"
      :model="formValidate"
      :rules="ruleValidate"
      :label-width="80"
      style="width: 500px;margin: 0 auto"
    >
      <FormItem label="姓名" prop="realName">
        <Input v-model="formValidate.realName" placeholder="请输入"></Input>
      </FormItem>
      <FormItem label="手机号" prop="phone">
        <Input v-model="formValidate.phone" placeholder="请输入"></Input>
      </FormItem>
      <FormItem label="身份证" prop="idCode">
        <Input v-model="formValidate.idCode" placeholder="请输入"></Input>
      </FormItem>
      <FormItem label="是否完成边民认证" prop="civil">
        <RadioGroup v-model="formValidate.civil">
          <Radio :label="true">是</Radio>
          <Radio :label="false">否</Radio>
        </RadioGroup>
      </FormItem>
      <FormItem>
        <Button type="primary" @click="handleSubmit('formValidate')"
          >提交</Button
        >
        <Button style="margin-left: 8px" @click="back">返回</Button>
      </FormItem>
    </Form>
  </Card>
</template>
<script>
import { userDetail, userUpdate } from '@/api/user.js'
import { SERVER_CODE } from '@/utils/constants.js'

export default {
  data() {
    return {
      formValidate: {
        id: null,
        realName: '',
        phone: '',
        idCode: '',
        civil: null
      },
      ruleValidate: {
        realName: [
          {
            required: true,
            message: '不能为空',
            trigger: 'blur'
          }
        ],
        phone: [
          {
            required: true,
            message: '不能为空',
            trigger: 'blur'
          }
        ],
        idCode: [
          {
            required: true,
            message: '不能为空',
            trigger: 'blur'
          }
        ]
        // civil: [{ required: true, message: '请选择', trigger: 'change' }]
      }
    }
  },
  mounted() {},
  activated() {
    this.getDetail()
  },
  methods: {
    getDetail() {
      let params = {
        id: this.$route.query.userId
      }
      console.log(params)
      userDetail(params)
        .then(({ code, data, msg }) => {
          if (code === SERVER_CODE.CODE_200) {
            console.log(data)
            this.formValidate.id = data.id
            this.formValidate.realName = data.realName
            this.formValidate.idCode = data.idCode
            this.formValidate.phone = data.phone
            this.formValidate.civil = data.civil
          } else {
            this.$Message.info(msg)
          }
        })
        .catch(error => {
          console.log(error)
        })
        .finally(() => {
          this.loading = false
        })
    },
    handleSubmit(name) {
      this.$refs[name].validate(valid => {
        if (valid) {
          userUpdate(this.formValidate)
            .then(({ code, msg }) => {
              if (code === SERVER_CODE.CODE_200) {
                this.$Message.success('用户编辑成功！')
                this.$store.state.userStatus = true
                setTimeout(() => {
                  this.$router.go(-1)
                }, 2000)
              } else {
                this.$Message.info(msg)
                console.log('用户编辑失败')
              }
            })
            .catch(error => {
              console.log(error)
            })
            .finally(() => {})
        }
      })
    },
    back() {
      this.$router.go(-1)
    }
  }
}
</script>
